<template>
<div class="seciton">
   <TopBar/>
  <div class='content'>
    <EditorTab/>
    <Preview/>
  </div>  
</div>

</template>
<script>
import TopBar from "./TopBar";
import EditorTab from "./EditorTab";
import Preview from "./Preview";
export default {
  components: {
    TopBar,
    EditorTab,
    Preview
  }
};
</script>
<style>
.seciton {
  /* height: 100%; */
  padding: 16px;
  height: 100vh;
  display: flex;
  flex-direction: column;
}
.content {
  margin-top: 16px;
  display: flex;
  height: 100%;
}
</style>




